import React,{ useState, useEffect } from 'react'
import { Link } from 'react-router-dom'
import { Form, Button } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Loader from '../components/Loader'
import Message from '../components/Message'
import FormContainer from '../components/FormContainer'
import { getUserDetails, updateUser } from '../actions/userActions'
import { USER_UPDATE_RESET } from '../constants/UserConstants'
import { useTranslation} from "react-i18next"

function UserEditScreen({ match, history}) {

    const userId = match.params.id
    const { t } = useTranslation()

    const [name, setName] = useState('')
    const [email, setEmail] = useState('')
    const [isAdmin, setIsAdmin] = useState(false)

    const dispatch = useDispatch()

    const userDetails = useSelector(state => state.userDetails)
    const {error, loading, user} = userDetails

    const userUpdate = useSelector(state => state.userUpdate)
    const { error: errorUpdate, loading:loadingUpdate, success: successUpdate } = userUpdate

    useEffect(() =>{

        if(successUpdate){
            dispatch({type:USER_UPDATE_RESET})
            history.push('/admin/userlist')
        }else{
            if(!user.name || user._id !== Number(userId)){
                dispatch(getUserDetails(userId))
            } else {
                setName(user.name)
                setEmail(user.email)
                setIsAdmin(user.IsAdmin)          
            }
        }

    }, [user, userId, successUpdate, history]) 

    const submitHandler = (e) => {
        e.preventDefault()
        dispatch(updateUser({id:user._id, name, email, isAdmin}))
    }
    return (
        <div className='margin-top-from-navbar'>
            <Link to='/admin/userlist'>
                {t('UserEditScreen_btn_go_back')}
            </Link>

            <FormContainer>
                <h1>{t('UserEditScreen_edit_user')}</h1>
                {loadingUpdate && <Loader/>}
                {errorUpdate && <Message variant='danger'>{errorUpdate}</Message>}
                {loading ? <Loader/> : error ? <Message variant='danger'>{error}</Message>
                : (
                    <Form onSubmit={submitHandler}>

                        <Form.Group controlId='name'>
                            <Form.Label>{t('UserListScreen_form_label_name')}</Form.Label>
                            <Form.Control
                                type='name'
                                placeholder ='Enter name'
                                value ={name}
                                onChange={(e) => setName(e.target.value)}
                            >
                            </Form.Control>
                        </Form.Group>

                        <Form.Group controlId='email'>
                            <Form.Label>Email Address</Form.Label>
                            <Form.Control
                                type='email'
                                placeholder ='Enter Email'
                                value ={email}
                                onChange={(e) => setEmail(e.target.value)}
                            >
                            </Form.Control>
                        </Form.Group>

                        <Form.Group controlId='isadmin'>
                            <Form.Check
                                type='checkbox'
                                label = {t('UserListScreen_form_label_admin')}
                                checked ={isAdmin}
                                onChange={(e) => setIsAdmin(e.target.checked)}
                            >
                            </Form.Check>
                        </Form.Group>

                        <Button type='submit' variant='primary' className='bnt-block bg-brown rounded my-3'>
                            {t('UserEditScreen_btn_update')}
                        </Button>

                    </Form>
                        
                    )}
                    
                </FormContainer>
        </div>
    )
}

export default UserEditScreen
