import React,{ useState, useEffect } from 'react'
import { LinkContainer } from 'react-router-bootstrap'
import { Table, Button, Row, Col } from 'react-bootstrap'
import { useDispatch, useSelector } from 'react-redux'
import Loader from '../components/Loader'
import Message from '../components/Message'
import Paginate from '../components/Paginate'
import { listProducts, deleteProduct, createProduct } from '../actions/productActions'
import { PRODUCT_CREATE_RESET } from '../constants/productsConstants'
import { useTranslation} from "react-i18next"

function ProductListScreen({ history }) {

    const dispatch = useDispatch()
    const { t } = useTranslation()
    const productList = useSelector(state => state.productList)
    const {loading, error, products, pages, page} = productList

    const productDelete = useSelector(state => state.productDelete )
    const {loading:loadingDelete, error:errorDelete, success:successDelete} = productDelete 

    const productCreate = useSelector(state => state.productCreate )
    const {loading:loadingCreate, error:errorCreate, success:successCreate, product:createdProduct} = productCreate 

    const userLogin = useSelector(state => state.userLogin)
    const { userInfo } = userLogin

    let keyword = history.location.search
    useEffect(() => {
        dispatch({type: PRODUCT_CREATE_RESET})

        if(!userInfo.IsAdmin){
            history.push('/login')
        }

        if(successCreate){
            history.push(`/admin/product/${createdProduct._id}/edit`)
        }else{
            dispatch(listProducts(keyword))
        }
        
    }, [dispatch, history, userInfo, successDelete, successCreate, createdProduct, keyword])


    const deleteHandler = (id) =>{
        if(window.confirm('Are you sure you want to delete this product?')){
            dispatch(deleteProduct(id))
        }
    }

    const createProductHandler = () =>{
        dispatch(createProduct())
    }

    return (
        <div className='mt-3'>
            <Row className='align-items-center'>
                <Col>
                    <h1>{t('ProductListScreen_title_products')}</h1>
                </Col>

                <Col className='btn-position-right'>
                    <Button className='bnt-block bg-brown rounded' onClick={createProductHandler}>
                        <i className='fas fa-plus'></i> {t('ProductListScreen_btn_create_product')}
                    </Button>
                </Col>
            </Row>

            {loadingDelete && <Loader/>}
            {errorDelete && <Message variant='danger'>{errorDelete}</Message>}

            {loadingCreate && <Loader/>}
            {errorCreate && <Message variant='danger'>{errorCreate}</Message>}
            
            {loading
            ? (<Loader/>)
            :error
                ? (<Message variant='danger'>{error}</Message>)
                : (
                    <div>
                    <Table striped bordered hover responsive className='table-sm'>
                        <thead>
                            <tr>
                                <th>ID</th>
                                <th>{t('ProductListScreen_table_name')}</th>
                                <th>{t('ProductListScreen_table_price')}</th>
                                <th>{t('ProductListScreen_table_category')}</th>
                                <th>{t('ProductListScreen_table_brand')}</th>
                                <th></th>
                            </tr>
                        </thead>

                        <tbody>
                            {products.map(product => (
                                <tr key={product._id}>
                                    <td>{product._id}</td>
                                    <td>{product.name}</td>
                                    <td>${product.price}</td>
                                    <td>{product.category}</td>
                                    <td>{product.brand}</td>

                                    <td>
                                        <LinkContainer to={`/admin/product/${product._id}/edit`}>
                                            <Button variant='success' className='btn-sm'>
                                                <i class="fas fa-pencil-alt"></i>
                                            </Button>                                     
                                        </LinkContainer>
                                            <Button variant='danger' className='btn-sm' onClick={() => deleteHandler(product._id)}>
                                                <i class="fas fa-trash"></i>
                                            </Button>   
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </Table>
                    <Paginate pages={pages} page={page} isAdmin={true}/>
                    </div>
                )}
        </div>
    )
}

export default ProductListScreen
